import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import $ from 'jquery';

class Navbar extends Component {
  componentDidMount(){
    var hdr = $('header.component--navigation-bar').height();
    $(window).scroll(function () {
      if ($(this).scrollTop() < hdr) {
        $('header.pinned.transparent').removeClass('scroll');
      } else {
        $('header.pinned.transparent').addClass('scroll');
      }
    });
    if ($('header.transparent').length == 1 && $('header.top').length == 0) {
      $('header.pinned').closest('.page').css("margin-top", hdr);
    }
  };

  render() {
    return (
      <header className="component component--navigation-bar pinned transparent top">
        <nav className="container-fluid navbar navbar-default">
          <div className="row">
            <div className="container">
              <div className="row  row--nav row--top">
              </div>
            </div>
            <div className="container">
              <div className="row  row--nav row--main">
                <div className="navbar-header navbar-left">
                  <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target=".bs-navbar-collapse" aria-expanded="false">
                    <span className="sr-only">Toggle navigation</span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                  </button>
                  <Link to="/" className="navbar-brand">
                    <img className="img-responsive" src={'/assets/images/logo.svg'} alt="himoza" />
                  </Link>
                </div>
                <div className="collapse navbar-collapse bs-navbar-collapse">
                  <ul className="nav navbar-nav navbar-center">
                    <li>
                      <Link to="/categories/hijab" >HIJAB</Link>
                    </li>
                    <li>
                      <Link to="/categories/clothing">CLOTHING</Link>
                    </li>
                    <li>
                      <Link to="/categories/accessories">ACCESSORIES</Link>
                    </li>
                    <li>
                      <Link to="/contact">CONTACT US</Link>
                    </li>
                  </ul>
                  <ul className="nav navbar-nav navbar-right">
                    <li><a href="#"><i className="fa fa-search"></i></a></li>
                  </ul>
                </div>
              </div>
            </div>
            <div className="container">
              <div className="row  nav-row row--bottom">
              </div>
            </div>
          </div>
        </nav>
      </header>
    );
  }
}

export default Navbar;



