import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import Homepage from './../Pages/Home';
import CategoriesHijab from './../Pages/Categories/hijab';
import CategoriesHijabDetails from './../Pages/Categories/hijab/Details';
import CategoriesClothing from './../Pages/Categories/clothing';
import CategoriesAccessories from './../Pages/Categories/accessories';
import PageContact from './../Pages/Contact';

class Routing extends Component {
  render() {
    return (
      <div>
        <Switch>
          <Route exact path="/" component={Homepage} />
          <Route exact path="/categories/hijab" component={CategoriesHijab} />
          <Route exact path="/categories/hijab/details" component={CategoriesHijabDetails} />
          <Route exact path="/categories/clothing" component={CategoriesClothing} />
          <Route exact path="/categories/accessories" component={CategoriesAccessories} />
          <Route exact path="/contact" component={PageContact} />
        </Switch>
      </div>
    )
  }
}

export default Routing;



