import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';
import Routing from './components/Route';
import './App.css';

class App extends Component {
  render() {
    return (
      <div>
        <BrowserRouter>
          <Routing/>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;



